import React, { useState, useEffect } from "react";
import { AuthenticatedTemplate, UnauthenticatedTemplate, useMsal } from "@azure/msal-react";
import { loginRequest, fetchCalendarEvents } from "./authConfig";
import { PageLayout } from "./components/PageLayout";
import { ProfileData } from "./components/ProfileData";
import { useInterval } from './useInterval';
import { callMsGraph } from "./graph";
import Button from "react-bootstrap/Button";
import "./styles/App.css";



const ProfileContent = () => {
    const { instance, accounts } = useMsal();
    const [graphData, setGraphData] = useState(null);
    const [calendarEvents, setCalendarEvents] = useState(null);
    const [status, setStatus] = useState('Available');
    const [latestEvent, setLatestEvent] = useState(null);
   
    function RequestProfileData() {
       instance.acquireTokenSilent({
         ...loginRequest,
         account: accounts[0],
       }).then((response) => {
         callMsGraph(response.accessToken).then((response) => setGraphData(response));
         fetchCalendarEvents(response.accessToken).then((response) => {
           setCalendarEvents(response.value);
         });
       });
    }
    useEffect(() => {
      
        if (calendarEvents) {
          handleCalendarEvents(calendarEvents);
        }
     }, [calendarEvents]);

     useEffect(() => {
        if (!calendarEvents || calendarEvents.length === 0) {
          setStatus('Available');
        }
     }, [calendarEvents]);
     async function handleCalendarEvents(events) {
        console.log("Events List ::::", events);
    
        if (!events || events.length === 0) {
            setLatestEvent(null);
            setStatus('Available');
            return;
        }
      
        const now = new Date()
        const upcomingEvents = events
            .filter(event => new Date(event.end.dateTime || event.end.date) >= now)
            .sort((a, b) => new Date(a.start.dateTime || a.start.date) - new Date(b.start.dateTime || b.start.date));

            console.log("upcomingEvents",upcomingEvents);
    
        if (upcomingEvents.length > 0) {
            const latestEvent = upcomingEvents[0];
            setLatestEvent(latestEvent);
    
            // Check if the current time is between event start and end times
            const eventStartTime = new Date(latestEvent.start.dateTime || latestEvent.start.date);
            const eventEndTime = new Date(latestEvent.end.dateTime || latestEvent.end.date);
            

            console.log("now",now.getTime());
            console.log("eventStartTime",eventStartTime.getTime());
            console.log("eventEndTime",eventEndTime);

            if (now.getTime() >= eventStartTime.getTime() && now.getTime() <= eventEndTime.getTime()) {
                setStatus('Busy');
                console.log("True")
            } else {
                if (latestEvent && latestEvent.showAs) {
                    const showAsLowerCase = latestEvent.showAs.toLowerCase();
                    const statusMapping = {
                        busy: 'Busy',
                        oof: 'Out of Office',
                        workingelsewhere: 'Vacation',
                        tentative: 'Tentative',
                        free: 'Available',
                    };
                    setStatus(statusMapping[showAsLowerCase] || 'Available');
                }
            }
        } else {
            setLatestEvent(null);
            setStatus('Available');
        }
    }
    
    return (
      
      <>
      
        <h5 className="card-title">Welcome {accounts[0].name}</h5>
        
        {graphData || latestEvent ? (
          <div>
            <ProfileData graphData={graphData} />
            <h6>Latest Calendar Event:</h6>
            {status && <p>Status: {status}</p>}
          </div>
        ) : (
          <Button variant="secondary" onClick={RequestProfileData}>
            Request Profile Information
          </Button>
        )}
      </>
    );
  };
const MainContent = () => {
  return (
    <div className="App">
      <AuthenticatedTemplate>
        <ProfileContent />
      </AuthenticatedTemplate>

      <UnauthenticatedTemplate>
        <h5 className="card-title">Please sign-in to see your profile information.</h5>
      </UnauthenticatedTemplate>
    </div>
  );
};

export default function App() {
  
  return (
    <PageLayout>
      <MainContent />
    </PageLayout>
  );
};
